@import "../settings/fonts";
@import "../settings/colors";

.register {
    &__column {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-top: 120px;
    }

    &__title {
        font-size: 2.5em;
    }

    &__deco {
        margin: 35px auto 50px;
    }

    // *Form* //
    &__form {
        display: flex;
        flex-direction: column;
        align-items: center;

        &-inputs {
            width: 384px;
            height: 328px;
            background-color: rgb(239, 239, 239);
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }

        &-label {
            font-size: 1em;
            font-weight: 600;
            height: 50px;
            display: flex;
            flex-direction: column;
            margin-bottom: 30px;
        }

        &-input {
            width: 250px;
            height: 50px;
            background-color: transparent;
            border: none;
            border-bottom: 1px solid  #707070;
        }
    }

    &__button {
        background-color: transparent;
        border: 0.75px solid $color-text;

        &:hover,
        &:active {
            box-shadow: inset 0 0 2px 0 $color-text;
            border: 0.75px solid $color-text;
        }
    }
}

.error__message {
    color: red;
    font-size: 0.8em;
    font-weight: 600;
    padding-bottom: 20px;
}